@import "../../styles/variables";
@import "../../styles/mixins";

.termsRoot {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background: $white;
  position: relative;
  .container {
    width: 100%;
    max-width: 742px;
    margin: 0 auto;
    box-sizing: border-box;
    @media(max-width: 998px) {
      padding: 0 30px; }
    @media(max-width: 600px) {
      padding: 0 20px; } }
  .header {
    padding: 30px;
    @include flex;
    align-items: center;
    justify-content: space-between;
    @media(max-width: 600px) {
      padding: 20px; }
    img {
      width: 209px;
      @media(max-width: 600px) {
        height: 40px;
        width: auto; } }
    a {
      font-size: $middleFont;
      font-weight: 500;
      border-radius: 3px;
      display: inline-block;
      padding: 15px 30px 18px;
      border: solid 1px rgba(0, 200, 83, 0.4);
      color: #00c853;
      text-decoration: none;
      @media(max-width: 600px) {
        padding: 10px 20px 10px; } } }
  .title {
    font-size: 44px;
    font-weight: 400;
    text-align: center;
    margin: 0;
    @media(max-width: 600px) {
      font-size: 32px;
      margin-top: 20px; } }
  .footer {
    width: 100%;
    padding: 0 30px;
    position: absolute;
    box-sizing: border-box;
    bottom: 0;
    @media(max-width: 600px) {
      position: relative;
      padding: 0 20px;
      margin-top: 30px; }

    div {
      width: 100%;
      padding: 30px 0;
      @include flex;
      align-items: center;
      justify-content: space-between;
      border-top: 1px solid rgba(115, 115, 139, 0.2);
      box-sizing: border-box;
      @media(max-width: 600px) {
        padding: 20px 0;
        flex-direction: column; } }
    p {
      margin: 0;
      font-size: $mainFont;
      color: $greyFont;
      @media(max-width: 600px) {
        text-align: center;
        margin: 10px 0; } } } }
